import { mapGetters } from 'vuex';
import Api from '@/common/api';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from '@/components/empty';
import { formatDate } from '@/common/date';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			pageNum: 1,
			headerPosition: 'fixed',
			headerTop: '0px',
			loadingType: 'more', //加载更多状态
			navList: [
				{
					text: '邀请列表',
					loadingType: 'more',
					list: []
				},
				{
					text: '收益',
					loadingType: 'more',
					list: []
				}
			]
		};
	},

	onLoad() {
		this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.pageNum = 1;
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.pageNum = this.pageNum + 1;
		this.loadData();
	},
	computed: {
		...mapGetters(['hasLogin'])
	},
	methods: {
		//获取订单列表
		async loadData(type = 'add', loading) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
				return;
			}

			let params = {
				pageNum: this.pageNum,
				status: status
			};
			if (type === 'refresh') {
				navItem.list = [];
			}
			let data=null;
			if(index==0){
				data = await Api.apiCall('get', Api.member.friendList, params);
			}else{
				data=await  Api.apiCall('get', Api.member.profitList, params);
			}
			
			const isfinish = navItem.list.length >= data.data.total;
			this.loadingType = isfinish ? 'nomore' : 'more';
			if (isfinish) return;
			let list = data.data.records;
			navItem.list.push(...list);
			if (type === 'refresh') {
				if (loading == 1) {
					uni.hideLoading();
				} else {
					uni.stopPullDownRefresh();
				}
			}
		},

		//顶部tab点击
		tabClick(index) {
			this.pageNum = 1;
			this.tabCurrentIndex = index;
			this.loadData('refresh');
		},
		showOrderModal(url, params, content, tabCurrentIndex) {
			let _this = this;
			uni.showModal({
				cancelText: '关闭',
				confirmText: '确定',
				content: content,
				async complete(res) {
					if (res.confirm) {
						let data = await Api.apiCall('post', url, params);
						if (data.code == 0) {
							_this.tabCurrentIndex = tabCurrentIndex;
							_this.pageNum = 1;
							_this.loadData('refresh');
						} else {
							_this.$api.msg(data.msg);
						}
					}
				}
			});
		}
	}
};
